<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main'}">
              Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ amount }} товара
      </span>
    </div>

    <section class="cart">
      <img :src="preloader" v-if="cartLoading" />
      <span v-else-if="cartLoadingFailed">Ошибка загрузки корзины</span>
      <form class="cart__form form" action="#" method="POST" v-else>
        <div class="cart__field">
          <ul class="cart__list">
              <CartItem v-for="item in products" :key="item.productId" :item="item" />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link
            tag="button"
            :to="{ name: 'order'}"
            class="cart__button button button--primery"
            type="submit"
            v-if="amount"
          >
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { mapGetters } from 'vuex';
import numberFormat from '@/helpers/numberFormat';
import CartItem from '@/components/CartItem';
import preloader from "@/assets/preloader.gif"

export default {
    name: 'CartPage',
    components: {
      CartItem
    },
    data() {
      return {
        preloader
      }
    },
    filters: {
      numberFormat
    },
    computed: {
      ...mapGetters({
        products: 'cartDetailProducts',
        totalPrice: 'cartTotalPrice',
        amount: 'cartAmount'
      }),
      cartLoading() {
        return this.$store.state.cartLoading;
      },
      cartLoadingFailed() {
        return this.$store.state.cartLoadingFailed;
      }
    }
}
</script>
