<template>
    <div class="form__counter">
        <button type="button" aria-label="Убрать один товар" @click.prevent="inputValue--">
            <svg width="12" height="12" fill="currentColor">
                <use xlink:href="#icon-minus"></use>
            </svg>
        </button>

        <input type="text" v-model.number="inputValue">

        <button type="button" aria-label="Добавить один товар" @click.prevent="inputValue++">
            <svg width="12" height="12" fill="currentColor">
                <use xlink:href="#icon-plus"></use>
            </svg>
        </button>
    </div>
</template>

<script>
export default {
    name: 'BaseCounterInput',
    props: ['counter'],
    model: {
        prop: 'counter',
        event: 'changeCounter'
    },
    methods: {
        changeCounter(counter) {
            if (counter > 0) {
                this.$emit('changeCounter', counter);
            }
        }
    },
    computed: {
        inputValue: {
            get() {
                return this.counter;
            },
            set(value) {
                this.changeCounter(value);
            }
        }
    }
}
</script>
