<template>
    <li class="cart__item product">
        <div class="product__pic">
            <img width="120" height="120" :src="item.product.image" :alt="item.product.title">
        </div>
        <h3 class="product__title">
            {{ item.product.title }}
        </h3>
        <span class="product__code">
            {{ item.product.id }}
        </span>

        <BaseCounterInput class="product__counter" v-model="amount" />

        <b class="product__price">
            {{ (item.product.price * item.amount) | numberFormat }} ₽
        </b>

        <button
            class="product__del button-del"
            type="button"
            aria-label="Удалить товар из корзины"
            @click.prevent="deleteProduct(item.productId)"
        >
            <svg width="20" height="20" fill="currentColor">
                <use xlink:href="#icon-close"></use>
            </svg>
        </button>
    </li>
</template>

<script>
import { mapMutations, mapActions } from 'vuex';
import numberFormat from '@/helpers/numberFormat';
import BaseCounterInput from '@/components/BaseCounterInput';

export default {
    name: 'CartItem',
    components: {
        BaseCounterInput
    },
    props: ['item'],
    filters: {
        numberFormat
    },
    computed: {
        amount: {
            get() {
                return this.item.amount;
            },
            set(value) {
                this.updateCartProductAmount({
                    productId: this.item.productId,
                    amount: value
                });
            }
        }
    },
    methods: {
        ...mapActions(['updateCartProductAmount', 'deleteProductFromCart']),
        deleteProduct(productId) {
            this.deleteProductFromCart(productId);
        }
    }
}
</script>
