<template>
    <router-link class="header__cart" aria-label="Корзина с товарами" :to="{ name: 'cart' }">
        <img :src="preloader" class="header__cart-preloader" v-if="cartLoading" />
        <span v-else>
            <svg width="30" height="21" fill="currentColor">
                <use xlink:href="#icon-cart"></use>
            </svg>
            <span class="header__count" aria-label="Количество товаров">
                {{ cartAmount }}
            </span>
        </span>
    </router-link>
</template>

<script>
import { mapGetters } from "vuex";
import preloader from "@/assets/preloader.gif"

export default {
    name: 'CartIndicator',
    data() {
        return {
            preloader
        }
    },
    computed: {
        ...mapGetters(['cartAmount']),
        cartLoading() {
            return this.$store.state.cartLoading;
        }
    }
}
</script>

<style>
.header__cart-preloader {
    max-width: 27px;
}
</style>
